main {
  border-radius:50px;
  box-shadow: 0px 0px 8px 0px rgb(0,0,0,0.5);
  color: white;
  background-color: rgb(5,51,107);
  width: 90%;

  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15px;

  padding: 20px;
}

center {
  margin-left: auto;
  margin-right: auto;
}


.background {
  background-color: rgb(188,210,232);
  height: 100%;
}

/* SEARCH BAR */
.searchBanner {
  background-color:rgb(5,51,107);
  text-align:center;
  padding: 20px;
}

.searchIcon {
  width: 20px;
  height: 20px;
}

#searchButton {
  background-color: rgb(55,92,137) ;

  width: 50px;
  height: 50px;

  border-radius: 25px;
  border: solid  rgb(82,138,174);
  
  padding: 10px;
  margin: 10px;
}

#searchInput {

  color: rgb(5,51,107);
  background-color:rgb(188,210,232);
  
  border-radius:25px;
  border: solid rgb(82,138,174);

  width: 20%;

  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 5px;
  padding-right: 5px;

  text-align: center;
}

::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: rgb(5,51,107);
  font-weight: bold;
  opacity: 1; /* Firefox */
}

::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: rgb(5,51,107);
  font-weight: bold;
  opacity: 1; /* Firefox */
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: rgb(5,51,107);
  font-weight: bold;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  font-weight: bold;
  color: rgb(5,51,107);
}

::-ms-input-placeholder { /* Microsoft Edge */
  font-weight: bold;
  color: rgb(5,51,107);
}

